import React from 'react'
import { MdSpaceDashboard, MdPeople, MdEvent, MdAttachMoney, MdGroups, MdVolunteerActivism,
   MdMessage, MdPermMedia, MdSettings, MdPerson } from "react-icons/md";
// Import or choose suitable icons for each item

const NAV_ITEMS = [
  { label: "Dashboard", icon: <MdSpaceDashboard /> },
   { label: "Profile", icon: <MdPerson /> },
  { label: "Members", icon: <MdPeople /> },
  { label: "Attendance", icon: <MdEvent /> },
  { label: "Events", icon: <MdEvent /> },
  { label: "Donations", icon: <MdAttachMoney /> },
  { label: "Groups", icon: <MdGroups /> },
  { label: "Volunteers", icon: <MdVolunteerActivism /> },
  { label: "Communication", icon: <MdMessage /> },
  { label: "Media", icon: <MdPermMedia /> },
  { label: "Settings", icon: <MdSettings /> },
   
];

const SideBarDash = () => {
  return (
    <div className='h-auto max-w-screen w-60 bg-persian-blue-400 pt-40'>
      <ul className=" flex flex-col ">
  {NAV_ITEMS.map(({ label, icon }) => (
    <li
            className="
             p-3
              transition-all duration-500
              flex
              pl-10
              items-center
              text-xl
              gap-3
              font-semibold
              
              group
              hover:bg-persian-blue-50 
              hover:text-persian-blue-600               
              hover:items-center"
            key={label}
          >
            <span className="group-hover:scale-110 transition-all duration-500">{icon}</span>
        <span className=" group-hover:font-bold group-hover:scale-105 transition-all duration-500">
{label}</span>
              
          </li>
        ))}
      </ul>
    </div>
  )
}

export default SideBarDash